import React from 'react';
import { Row, Col, Card, Table, Button } from 'react-bootstrap';
import { Link } from "react-router-dom"
import axios from 'axios'
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import
import Aux from "../../../hoc/_Aux";
import UpdatePrivilege from './Update.Privilege';
import i18n from './../../../i18n';

var urlbackend = require('../../../env/env')();


class ListPrivilege extends React.Component {
    state = {
        privileges: [],
    };
  
    componentDidMount() {

        const config = {
            headers: {
              'Content-Type': 'application/json',
              'Access-Control-Allow-Headers': 'Authorization',
              "x-access-token": localStorage.getItem('token'),
              'Authorization': 'Bearer '+localStorage.getItem('token'),
            },
          };
      
          axios.get(urlbackend + "/privileges", config)
            .then(response => {
              console.log("this respon list", response.data)
              this.setState({ privileges: response.data.data })
      
            })
            .catch(e => {
              console.log(e)
            })
        
    }

    Delette(privilege){
        console.log("hellodelet", privilege.id)
        confirmAlert({
            title: 'Confirm Delete',
            message: 'Are you sure to do Delete?',
            buttons: [
              {
                label: 'Yes',
                onClick: () => {
                    axios.delete("http://localhost:5000/api/v1/privileges/"+privilege.id).then(()=>{
            
                        var array = [...this.state.privileges]; 
                        var index = array.indexOf(privilege)
                        if (index !== -1) {
                          array.splice(index, 1);
                          this.setState({privileges: array});
                        }
            
                   
                    })
                }
              },
              {
                label: 'No',
                onClick: null
              }
            ]
          });
      /*  */
    }
    render() {
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">   <Link to="/Add/privilege" >  <Button>{i18n.t('add-privilege')}</Button> </Link> </Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <Table striped responsive>
                                    <thead>
                                        <tr>
                                            <th>{i18n.t('privilege')}</th>
                                            <th>{i18n.t('description')}</th>
                                            <th>{i18n.t('action-event')}</th>

                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.state.privileges.map((privilege, index) => (

                                            <tr key={privilege.id} >
                                               
                                                <td>{privilege.name} </td>
                                                <td>{privilege.description} </td>
                                                <td>  <Button variant="danger"
                                                    onClick={()=> this.Delette(privilege)}> <span className="fa fa-trash"></span></Button> 
                                                    
                                               <UpdatePrivilege privilege={privilege} change={()=>this.componentDidMount()} />
                                                    </td>
                                            </tr>


                                        ))}
                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    }
}

export default ListPrivilege;